import { PrimaryButton,TextField,Stack,Text, Label,DefaultEffects,Nav } from '@fluentui/react';
import React,{useState } from 'react';
import logo from './logo.svg';


function App() {
 
  const [state,setState] = useState({
    curAvgPrice:0,
    curCount: 0,
    curTotalPrice: 0,
    addAvgPrice:0,
    addCount: 0,
    addTotalPrice: 0,
    resultAvgPrice:0,
    resultCount: 0,
    resultTotalPrice: 0
  });
 
  function curCalcul(e) {
    const value = parseInt( e.target.value);
    let newState = {...state, [e.target.name]:value};
    newState.curTotalPrice = newState.curCount*newState.curAvgPrice;
    newState.addTotalPrice = newState.addCount*newState.addAvgPrice;
    newState.resultCount = newState.curCount + newState.addCount;
    newState.resultTotalPrice = newState.curTotalPrice+ newState.addTotalPrice;
    newState.resultAvgPrice =  newState.resultTotalPrice  / newState.resultCount ;
    setState(newState);
  }
  const navLinkGroups = [
    {
      name: '',
      links: [
        { name: ' ', url: '', key: 'key1', target: '_blank' },
     
      ],
    },
   
  ];
  return (
    <div className="App">
    <Stack horizontal>
   
     <Nav  groups={navLinkGroups} styles={{ width:400 }}/>
      
     <Stack tokens={{padding:50 ,childrenGap:10 }}>
      <Stack.Item align='center' tokens={{padding:50,childrenGap:10 }} style={{ boxShadow: DefaultEffects.elevation8,backgroundColor:'white'}}>
        <Text variant="superLarge" block style={{marginBottom:40}}> </Text>
        <Text variant="large">
          
        </Text>
        <Text variant="small"  style={{paddingLeft:10,color:'gray'}}>
        (   ,  .)
        </Text>
        <Stack horizontal tokens={{padding:10,childrenGap:10}}  >
          <TextField name='curAvgPrice' onChange={function(e){ curCalcul(e); } } placeholder=' '  suffix=""/>
          <TextField name='curCount' onChange={function(e){ curCalcul(e); } } placeholder=' ' suffix="" />
          <TextField defaultValue={0} value={`${state.curTotalPrice}`} suffix=""/>
        </Stack>
        <Text variant="large">
         
        </Text>
        <Text variant="small"  style={{paddingLeft:10,color:'gray'}}>
        (   ,  .)
        </Text>
        <Stack horizontal tokens={{padding:10,childrenGap:10}}   >
          <TextField name='addAvgPrice' onChange={function(e){ curCalcul(e); } } placeholder=''  suffix=""/>
          <TextField name='addCount' onChange={function(e){ curCalcul(e); } } placeholder='' suffix="" />
          <TextField defaultValue={0} value={`${state.addTotalPrice}`} suffix=""/>
        </Stack>
        <Text variant="large">
        
        </Text>
        <Text variant="small"  style={{paddingLeft:10,color:'gray'}}>
        (  .)
        </Text>
        <Stack horizontal tokens={{padding:10,childrenGap:10}} style={{backgroundColor:"pink"}}>
          <TextField readOnly name='resultAvgPrice' value={`${state.resultAvgPrice}`} placeholder=''  suffix=""/>
          <TextField readOnly name='resultCount' value={`${state.resultCount}`} placeholder='' suffix="" />
          <TextField readOnly defaultValue={0} value={`${state.resultTotalPrice}`} suffix=""/>
        </Stack>
        </Stack.Item>
      </Stack>
      </Stack>
    </div>
  );
}

export default App;
